<template>
  <div class="personal">
    <el-tabs tab-position="left" type="border-card">
    <el-tab-pane label="账户安全">
      <h1>账户安全</h1>
      <h2>个人信息</h2>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="手机">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item @click="onSubmit">
          <el-button type="primary">修改</el-button>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="我的收藏">
      <h1>我的收藏</h1>
      <div class="collection-container">
        <div v-for="item in collectionList" class="collection-block" :key="item.title">
          <img :src="item.imagePath" alt="" width="240px" height="300px">
          <div class="title">{{item.title}}</div>
        </div>
      </div>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>
<script>
export default {
  data () {
    return {
      username: '',
      form: {
        phone: ''
      },
      collectionList: []
    }
  },
  mounted () {
    this.getUserInfo()
    this.getCollection()
  },
  methods: {
    getUserInfo () {
      this.username = this.$route.params.username
      this.$http.get('/user/' + this.username).then(res => {
        console.log(res.data)
        this.form = res.data.data
      })
    },
    getCollection () {
      this.$http.get('/user/' + this.username + '/collections').then(res => {
        console.log(res.data)
        this.collectionList = res.data.data
      })
    },
    onSubmit () {
      this.$http.post('/user/' + this.username, this.form).then(res => {
        const json = res.data
        if (json.code === 200) {
          this.$message.success('修改成功')
        } else {
          this.$message.error('修改失败')
        }
      })
    }
  }
}
</script>
<style scoped>
.collection-container{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.collection-block{
  width: 250px;
  height: 400px;
  background-color: #ff0000;
}
</style>
